.talent {
  width: 100%;
  height: 520px;
  background-color: #e9f1ff;
}
.content-2 {
  text-align: center;
  padding-top: 20px;
}
.content-2 h1 {
  font-weight: bold;
  font-size: 50px;
}
.content-2 p {
  font-size: 16.5px;
  color: #757783;
}

.containers {
  width: 100%;
  height: 300px;
  display: flex;
  justify-content: center;
  align-items: center; 
  justify-content: space-around; 
  flex-direction: row;
}
.item span {
  font-size: 30px;
}
.item h6 {
  font-weight: bold;
  font-size: 19px;
}
.item p {
  padding: 10px;
}
.item {
  background-color: #ffbe33;
  border: 5px solid #ffffff;
  border-radius: 10px;
  padding: 10px;
  text-align: center;
  width: 258px;
  height: 283px;
}

@media only screen and (max-width: 800px) {
  .talent {
    width: 100%;
    height: 1020px;
    background-color: #e9f1ff;
  }
  .content-2 {
    text-align: center;
    padding-top: 20px;
  }
  .content-2 h1 {
    font-weight: bold;
    font-size: 25px;
  }
  .content-2 p {
    font-size: 12px;
    color: #757783;
  }

  .containers {
    width: 100%;
    height: 850px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }
  .item {
    background-color: #ffbe33;
    border: 5px solid #ffffff;
    border-radius: 10px;
    padding: 5px;
    text-align: center;
    width: 374px;
    height: 236px;
    margin-bottom: 10px;
  }
  .item span {
    font-size: 30px;
    color: blue;
  }
  .item h6 {
    font-weight: bold;
    font-size: 17px;
  }
  .item p {
    font-size: 17px;
  }
  
}
