.links {
    width: 100%;
    height: 580px;
    background-color: #21325b;
    display: flex;
    justify-content: center;
    align-items: center;
    justify-content: space-evenly;
    flex-direction: row;
  }
  .about {
    width: 300px;
    height: 520px;
  
  }
  .quick-links {
    width: 180px;
    height: 300px;
    position: relative;
    bottom: 20px;
  }
  .in-about {
    width: 300px;
    height: 64px;
    background-image: url("../images/logo2.png");
    background-position: center;
    background-repeat: no-repeat;
    background-size: 60%;
    margin-top: 50px;
  }
  .in-about-1 {
    width: 300px;
    height: 300px;
  }
  .in-about-1 p {
    color: #ffffff;
    line-height: 30px;
    text-align: left;
    padding-top: 20px;
    margin-left: 20px;
  }
  .social-icon {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .about,
  .quick-links,
  .courses,
  .contact {
    float: left;
   
  }
 
  .contact {
    width: 360px;
    height: 400px;
    margin-top: 60px;
    line-height: 30px;
  }
  .contact-table {
    color: #ffffff;
    margin-left: 20px;
  }
  
  .contact-table td {
    border-bottom: 1px solid #93959e;
  }
  .social-icon .social-icon1 {
    transition: transform 0.1s;
  }
  .social-icon:hover .social-icon1 {
    transform: scale(1.2);
  }
  .social-icon .social-icon2 {
    transition: transform 0.1s;
  }
  .social-icon:hover .social-icon2 {
    transform: scale(1.2);
  }
  .social-icon .social-icon3 {
    transition: transform 0.1s;
  }
  .social-icon:hover .social-icon3 {
    transform: scale(1.2);
  }
  .social-icon .social-icon4 {
    transition: transform 0.1s;
  }
  .social-icon:hover .social-icon4 {
    transform: scale(1.2);
  }
  @media only screen and (max-width: 800px) {
    .links {
      width: 100%;
      height: 1680px;
      flex-direction: column;
    }
    .about,
    .quick-links,
    .courses,
    .contact {
     margin-left: -30px;
      width: 300px;
    }
    .quick-links {
      margin-top: 30px;
      margin-left: -30px;
      width: 180px;
      height: 300px;
      position: relative;
      left: -60px;
      bottom: 0px;
    }
    .social-icon .social-icon1 {
      transition: transform 0.1s;
    }
    .social-icon:hover .social-icon1 {
      transform: scale(1.2);
    }
    .social-icon .social-icon2 {
      transition: transform 0.1s;
    }
    .social-icon:hover .social-icon2 {
      transform: scale(1.2);
    }
    .social-icon .social-icon3 {
      transition: transform 0.1s;
    }
    .social-icon:hover .social-icon3 {
      transform: scale(1.2);
    }
    .social-icon .social-icon4 {
      transition: transform 0.1s;
    }
    .social-icon:hover .social-icon4 {
      transform: scale(1.2);
    }
    
  }
  