.footer {
    width: 100%;
    height: 60px;
    background-color: #1c1e27;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .footer p {
    font-size: 20px;
    color: #aeb2c2;
    padding-top: 10px;
  }
  @media only screen and (max-width: 800px){
    .footer{
        height: 100%;
    }
    .footer p{
      font-size: 17px;
    }
  }