.placements {
  width: 100%;
  height: 342px;
  background-color: #e9f1ff;
 
}

.content-3 {
  text-align: center;
  padding-top: 35px;
 
}
.content-3 h1 {
  font-weight: bold;
  font-size: 50px;
}
.content-3 p {
  font-size: 16.5px;
  color: #757783;
}
.companies {
  padding-top: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  justify-content: space-evenly;
  flex-direction: row;
}

.compaines-1,
.compaines-2,
.compaines-3,
.compaines-4,
.compaines-5 {
  float: left;
}

.compaines-1 {
  width: 202px;
  height: 68px;
  border: 1px solid #e8bc19;
  background-image: url("../images/Capture-01.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-color: #ffffff;
}
