@media only screen and (max-width: 800px) {
  .placements {
    width: 100%;
    height: 650px;
  }
  .content-3 {
    text-align: center;
   
  }
  .content-3 h1 {
    padding-top: 0%;
    font-weight: bold;
    font-size: 30px;
  }
  .content-3 p {
    font-size: 16.5px;
    color: #757783;
  }
  .companies {
    padding-top: 0%;
    flex-direction: column;
  }
  .compaines-1,
  .compaines-2,
  .compaines-3,
  .compaines-4,
  .compaines-5 {
    margin-bottom: 13px;
  }
}
