* {
    margin: 0;
    padding: 0;
  }
  
  body {
    font-family: "Kumbh Sans", sans-serif;
  }
  
  #trans-img {
   
    width: 50%;
    height: 500px;
    display: flex;
    justify-content: center;
    align-items: center;
    float: left;
  }
  #blue {
    background-color: #4d5fe3;
    width: 20px;
    height: 350px;
    border-radius: 10px 0px 0px 10px;
    float: left;
  }
  .sub-container-one {
        

    height: 400px;
    width: 250px;
    overflow: hidden;
    border-radius: 15px;
}

.sub-container-one div {
  background-image: url(../images/img3\(1\).jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 400px;
    display: block;
    transition: transform 2s;
}

.sub-container-one:hover div {
    transform: scale(1.1);
}

.sub-container-two {
   

    height: 400px;
    width: 250px;
    overflow: hidden;
    border-radius: 15px;
}

.sub-container-two div {
  background-image: url(../images/img3\(2\).jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 400px;
    display: block;
    transition: transform 2s;
}

.sub-container-two:hover div {
    transform: scale(1.1);
}
  .about-content {
    
    width: 50%;
    height: 500px;
    float: right;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  #readmore{
      width: 150px;
   }
  
