.table-container {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .content-1 h1{
    border: 1px solid white;
    text-align: center;
   font-weight: bold;
   font-size: 50px
  }
  .content-1 p{
    text-align: center;
    font-size: 16.5px;
    color: #757783;
  }
  .search-input {
    border: 1px solid #93959e;
    width: 350px;
    padding: 6px;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    margin-right: -8px;
    margin-bottom: 18px;
  }
  
  .buttons {
    width: 350px;
    margin-left: 32px;
    padding: 6px;
    background-color: #ffffff;
    border-radius: 0%;
    border: 1px solid #757783;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    box-sizing: content-box;
  }
  
  .featured-dropdowns {
    width: 380px;
  }
  
  @media only screen and (max-width: 800px){
   .content{
   margin-top: 350px;
   }
  .content-1{
    width: 100%;
    border:1px solid white;
    
  }
  .content-1 h1{
    margin-top:15px;
    height: 30px;
    text-align: center;
    font-size: 20px;
  }
  .content-1 p{
    text-align: center;
    font-size: 15px;
  }
  .table-container{
    display: none;
  }
  }